.notes-list-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.notes-list {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    max-width: 660px;
}

.note {
    margin: 0.5rem 0.5rem;
    width: 200px;
    min-height: 170px;
    overflow: hidden;
    border: 1px solid black;
    padding: 0.5rem 1rem;
    background-color: white;
}

.note h4 {
    margin: 0;
}

.note hr {
    margin: 0.25rem 0 0.5rem 0;
}

.note p {
    margin: 0;
    height: 6.8rem;
    overflow: hidden;
}


