.left-nav-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 1rem;
    /* min-width: 220px;
    max-width: 220px; */
    width: 220px;
    height: 100vh;
    background-color: #D3D3D3;
}

.left-nav-header {
    margin: 0;
}

.left-nav-buttons-container {
    margin: 0.5rem 0 0 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;    
}

.left-nav-button {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    color: white;
    font-weight: 300;
    background-color: #23B8BE;
    border: 1px solid #86AEB1;
    margin: 0.5rem 0;
    height: 40px;
    border-radius: 0.1rem;
}


