* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Open Sans', 'Helvetica Neue', sans-serif;
  /* border: 1px solid red; */
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', 'Helvetica Neue', sans-serif;
  color: rgb(66,66,66);
}

.app-container {
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  min-width: 900px;
}

.content-header {
  margin: 1rem 0.5rem;
}

.button-link {
  text-decoration: none;
}

.unstyled-link {
  text-decoration: none;
  color: rgb(66,66,66);
}

.unstyled-link :visited {
  text-decoration: none;
  color: rgb(66,66,66);
}

.button {
  width: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: white;
  font-weight: 600;
  background-color: #23B8BE;
  border: 1px solid #86AEB1;
  margin: 0.5rem 0.5rem;
  height: 40px;
  border-radius: 0.1rem;
}

.button--danger {
  background-color: red;
}


